<template>
    <div class="content" :class="{'cntDone':isDone}" id="content">
        <h1 class="myName">Feng</h1>
        <p class="sentence">{{hitokoto}}</p>
        <p class="from">-- {{from}}</p>
        <p class="link"><a href="https://github.com/dykily/blog/issues" target="_blank">Blog</a> </p>
        <p class="link"><a href="https://github.com/dykily" target="_blank">Github</a> </p>
        <p class="link"><a href="https://juejin.im/user/577e6a49128fe10056591bf1" target="_blank">掘金</a> </p>
        <p class="link"><a href="https://www.zhihu.com/people/dykliy/activities" target="_blank">知乎</a> </p>
    </div>
</template>

<script>
export default {
  name: 'Info',
  mounted() {
    this.getHitokoto();
    
  },
  methods: {
    getHitokoto(){
      this.$axios.get("https://v1.hitokoto.cn").then((res)=>{
        if(55 > res.data.hitokoto.length){
          this.hitokoto = res.data.hitokoto;
          this.from = res.data.from;
          this.isDone = true;
        }else{
          
          this.getHitokoto();

        }
        
      }).catch((e)=>{
        this.errCount++;
        if(this.errCount < 5){
          setTimeout(()=> this.getHitokoto(),100);
        }else{
          this.hitokoto = "对面的小霸王服务器接口出问题了";
          this.from = e;
          this.isDone = true;
        }
        
      });
    }
  },
  data(){
    return {
      hitokoto:"",
      from:"",
      isDone:false,
      errCount:0,
      lastTime:0,
    }
  }
}
</script>

