<template>
  <div id="app">
    <div>
      <div style="margin-bottom: 15px;" v-show="!showGame">
        <p style="margin-bottom: 10px;">我可以走进你心里信不信？<br/>不信我们就玩个游戏吧</p>
        <span class="select-btn" @click="showGame = true">PLAY GAME</span>
      </div>
      <div v-show="showGame">
        <p>下面有五组数字，心里任想一个数字，然后你告诉我每组是否有你想的数字</p>
        <p>我就会走进你心里把那个数字取出来</p>
        <div class="">
          <div>A组：19、27、16、21、22、26、20、17、18、24、29、25、23、30、28</div>
          <div>B组：12、24、30、28、9、14、25、27、15、8、26、29、10、11、13</div>
          <div>C组：6、21、7、14、12、22、4、20、29、28、23、15、13、30、5</div>
          <div>D组：22、18、14、30、19、6、23、3、7、10、27、2、26、11、15</div>
          <div>E组：3、5、19、15、1、21、17、7、27、9、11、25、23、29、13</div>
        </div>
        <div class="answer">
          <div v-if="step < 5">
              <div class="question">你心里所想的数字是否在 <span class="inline-block bounce-enter-active" style="color: #67C23A;">{{ getNumType(step) }}</span> 组？</div>
              <span class="select-btn" @click="selectYesOrNo(1)">在</span><span class="select-btn" @click="selectYesOrNo(0)">不在</span>
          </div>
          <div class="result" v-else>
            <div class="result-num" v-if="result === -1" style="color:#E6A23C;">你选择的数字不对，我走不进你的心里</div>
            <div class="result-num" v-else>我已经走进你的左心房读到的数字是：<div style="color: #F56C6C;display:inline-block;" class="bounce-enter-active">{{ result }}</div></div>
            <span class="select-btn" @click="resetStep">重来</span>  <span class="select-btn" @click="outGame">没意思走了</span>
          </div>
        </div>
      </div>
    </div>
    <Info v-show="!showGame"/>
  </div>
</template>

<script>
import Info from './components/Info.vue'

export default {
  name: 'app',
  components: {
    Info
  },
  data() {
    return {
      question: '',
      step: 0,
      answer: [],
      result: 0,
      showGame: false,
      allResult: [19, 27, 16, 21, 22, 26, 20, 17, 18, 24, 29, 25, 23, 30, 28, 12, 9, 14, 15, 8, 10, 11, 13, 6, 7, 4, 5, 3, 2, 1],
    }
  },
  methods: {
    getNumType(step) {
      switch(step) {
        case 0:
          return 'A'
        case 1:
          return 'B'
        case 2:
          return 'C'
        case 3:
          return 'D'
        case 4:
          return 'E'
        default:
          return ''
      }
      
    },
    selectYesOrNo(yes) {
      this.step++
      // this.question = '你不要乱想好不好，我真的很喜欢你的 Y'
      this.answer.push(yes)

      if (this.step >= 5) {
        const result = parseInt(this.answer.join(''), 2)
        if (this.allResult.includes(result)) {
          this.result = result
        } else {
          this.result = -1
        }
      }
    },
    resetStep() {
      this.step = 0
      this.answer = []
      this.result = 0
    },
    outGame(){
      this.resetStep()
      this.showGame = false
    }
  },
}
</script>

