    width: 100%;
    height: 100%;
    /* background: #1b1f22; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    box-sizing: border-box;
}
h1,p{
  margin: 0;
  padding: 0;
}
.answer {
  margin: 10px 0;
}
.question, .result-num {
  margin-bottom: 15px;
}
.select-btn {
  margin-right: 10px;
  background: #409EFF;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
  color: #fff;
}
.result-num {
  animation: bounce-in 1s;
}
.bounce-enter-active {
  animation: bounce-in 1s infinite;
}
@keyframes bounce-in {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}
.inline-block {
  display: inline-block
}
</style>
